const surveyTest = window.document.querySelector('.survey-test');

export const surveyTestInit = () => {
  if (surveyTest) {
    const btnGoToStep2 = document.getElementById('goToStep2');
    const btnGoToStep3 = document.getElementById('goToStep3');
    const btnSubmit = document.getElementById('submitBtn');
    const submitSpinner = document.getElementById('submitSpinner');
    const btnResults = document.querySelector('.btn-results');
    console.log(btnResults);
    console.log(btnResults.href);

    const tab2 = document.getElementById('nav-step2-tab');
    const tab3 = document.getElementById('nav-step3-tab');

    // eslint-disable-next-line no-undef
    const tab2Trigger = new bootstrap.Tab(tab2);
    // eslint-disable-next-line no-undef
    const tab3Trigger = new bootstrap.Tab(tab3);

    const scrollToTop = () => {
      window.scroll({
        top: 0,
        behavior: 'smooth',
      });
    };

    btnGoToStep2.addEventListener('click', (event) => {
      event.preventDefault();
      scrollToTop();
      setTimeout(() => {
        tab2Trigger.show();
      }, 1000);
    });

    btnGoToStep3.addEventListener('click', (event) => {
      event.preventDefault();
      scrollToTop();
      setTimeout(() => {
        tab3Trigger.show();
      }, 1000);
    });

    btnSubmit.addEventListener('click', (event) => {
      event.preventDefault();
      event.target.disable = true;
      submitSpinner.classList.remove('d-none');

      setTimeout(() => {
        btnResults.click();
      }, 3000);
    });
  }
};
